:root{
    --maincolor:#8b008b;
}
header{
    background-color: var(--maincolor);
}
.container{
    width: 95%;
    /* background-color: orange; */
    margin: 0 auto;
    max-width: 1200px;
}
.container-flex{
    display: flex;
    justify-content: space-around;
    align-items: center;
    }
.logo{
    width: 100px;
    padding: .5em 0;
}
.own{
    width: 100px;
    padding: .5em 0;

}

.list{
    display: flex;
}
.listitem{
    margin-left: 2rem;
    color: white;
    text-decoration: none;
    font-size: 1.5rem; font-family: 'Poppins', sans-serif;
}
.activeItem{
    color: orange;
    border-bottom: .1em double #fff;
}
.icon{
    color: #fff;
    font-size: 1.5rem !important;
    margin-right: 1em;
    margin-left: 1.5em;
}
/* Media queries */
@media screen and (max-width:1100px){

    .icon{
                margin-left: 1em;
    }
    
}
    
@media screen and (max-width:1100px){
    html{
        font-size: 13px;
    }
    .container-flex{
        flex-direction: column;
        }
        .logo{
            width: 100px;
                   }
                   .own{
                    width: 100px;
                                  }

}
@media screen and (max-width:568px){

    html{
        font-size: 11px;
    }
    .list{
        flex-direction: column;
    }
    .listItem{
        margin-top: .2em;
    }
    .logo{
        width: 80px;
    }
    .own{
        width: 80px;
       
    }
.icon{
    margin: 1em 1.5em;
}

}
